.news-card {
    border: 0px;
    overflow: hidden;
    border-radius: 15px;
    box-shadow: 0 3px 45px 0 rgba(63,92,117,0.10);

    .card-img-top {
        height: 170px;
        background-color: #000;

        iframe {
            height: 100%;
        }
    }

    .card-body {
        min-height: 200px;
    }

    p {
        font-size: 15px;
        line-height: 30px;
        color: #4F5B64;
    }
}