.header-banner {
    background-image: url('./dist/images/bg.jpg');
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    color: #fff;

    &.mini-banner {
        background: none;
        &::before {
            content: "";
            background-image: url('./dist/images/bg.jpg');
            background-attachment: fixed;
            background-position: center;
            background-size: cover;
            position: absolute;
            display: block;
            z-index: -1;
            min-height: 900px;
            height: 100vh;
            width: 100%;
            top: 0;
            left: 0;
        }

        .container {
            justify-content: flex-start;
            padding-top: 170px;
            min-height: unset;
            height: auto;
        }
    }

    .container {
        display: flex;
        height: 100vh;
        min-height: 900px;
        text-align: center;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: relative;
    }

    .header-title {
        color: inherit;
        font-size: 80px;
        line-height: 96px;
        text-shadow: 0 10px 7px rgba(0,0,0,0.2);
        text-shadow: 0 8px 11px rgba(0,0,0,0.15);
    }

    .header-slogan {
        color: inherit;
        font-weight: 700;
        font-size: 21px;
        line-height: 25px;
    }

    .social-links-wrapper {
        top: 0;
        left: 0;
        height: 100%;
        color: #fff;
        display: flex;
        position: absolute;
        flex-direction: column;
        justify-content: space-between;

        & > div {
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .text-established {
            margin: 0;
            color: inherit;
            font-weight: 700;
            transform: rotate(-90deg);
        }

        
    }
}

.social-link {
    display: block;
    color: inherit;
    height: 36px;
    width: 36px;
    display: flex;
    align-items: center;
    border-radius: 50%;
    margin-bottom: 30px;
    justify-content: center;
    border: 1px solid #fff;

    &:hover, &:focus, &:focus:active {
        outline: none;
        text-decoration: none;
        color: $color__orange;
        background-color: #fff;
    }

    &:last-of-type {
        margin-bottom: 0px;
    }
}

.lined-title {
    margin-bottom: 25px;
    

    &.text-center:before {
        margin-left: auto;
        margin-right: auto;
    }

    &:before:not(.no-line) {
        content: '';
        display: block;
        width: 50px;
        height: 6px;
        background-color: $color__aquagreen;
        margin-bottom: 20px;
    }

    h3 {
        font-size: 36px;
        font-weight: 700;
    }

    h6 {
        font-size: 18px;
        font-weight: 600;
    }
}