/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
@import "links";

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
@import "menus";


.header-navbar {
    background-color: transparent;
    position: absolute;
    z-index: 1;
    top: 50px;
    width: 100%;

    .navbar-nav {
        .nav-item {
            display: flex;
            align-items: center;
            margin: 0px 35px;

            &.current-menu-item:not(.btn-order-now) a {
                color: $color__orange;
            }
        }

        .nav-item a {
            font-size: 16px;
            font-weight: 700;
            line-height: 19px;
            color: #fff;

            &:hover {
                text-decoration: none;
            }
        }
    }

    .logo {
        height: 73px;
    }
}
.navbar-collapse {
    .navbar-nav {
        @media screen and (max-width: 991px) {
            background-color: $color__text-main;
            padding: 20px 0px 40px;
            width: 250px;
        }

        li:not(:last-of-type) a {
            @media screen and (max-width: 991px) {
                width: 100%;
                padding: 20px 0px;
            }
        }
    }
}

.navbar-toggler {
    border: 0px;

    &:focus, &:focus:active {
        box-shadow: none;
        outline: none;
    }

    .fas.fa-bars {
        color: #fff;
        font-size: 30px;
    }
}