.dotted-indicator-carousel {
    .carousel-indicators {
        margin-top: 55px;
        position: static;

        li {
            opacity: 1;
            width: 12px;
            height: 12px;
            margin: 12px;
            border-radius: 50%;
            background-color: '#fff';
            border: 2px solid $color__aquagreen;
            
            &.active {
                background-color: $color__aquagreen;
            }
        }
    }
}

.carousel-control-next,
.carousel-control-prev {
    width: auto;
    opacity: 0.4;

    &:hover { 
        opacity: 1;
    }
}

.carousel-control-prev {
    margin-left: -10px;
}

.carousel-control-next {
    margin-right: -10px;
}

.control-icons {
    height: 86px;
    width: 86px;
    border-radius: 50%;
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 33px 0 rgba(26,39,50,0.10);
    
    background-image: none;
    background-color: #fff;
    color: $color__aquagreen;

    &.--primary {
        border: 0px;
        color: #fff;
        background-color: $color__aquagreen;

        &:hover {
            background-color: darken($color__aquagreen, 5);
        }

        &:focus, &:focus:active { 
            outline: none;
        }

        &:disabled, &.disabled, &[disabled] {
            background-color: $color__disabled-button;
        }
    }
    
    i.fas {
        font-size: 30px;
    }
}