.table.--aquagreen {
    &.single-table {
        td, th {
            &:nth-of-type(2) {
                text-align: left;
            }
        }
    }

    th {
        color: #fff;
        text-align: right;
        vertical-align: middle;
        border-color: $color__aquagreen;
        background-color: $color__aquagreen;

        &:first-of-type {
            text-align: center;
        }
    }

    tr:nth-of-type(odd) {
        background-color: rgba(#71B3BA, .10);
    }

    td {
        border: 0px;
        text-align: right;
        vertical-align: middle;

        &:first-of-type {
            color: $color__aquagreen;
            text-align: center;
            font-weight: 600;
        }
    }

    .btn-action {
        color: #fff;
        font-size: 14px;
        padding: 10px 12px;
        border-radius: 8px;
        background-color: $color__aquagreen;
    }
}

.table-title  {
    margin-bottom: 15px;

    h5 {
        margin: 0;
        font-size: 18px;
        font-weight: 700;
        color: $color__aquagreen;
    }

    p {
        margin: 0;
        font-size: 14px;
        font-weight: 600;
        line-height: 1.2;
    }
}
