.site-footer {
    background-color: #f9f9f9;

    p {
        font-size: 14px;
        margin: 0px;
    }

    .text-address {
        text-align: right;
    }

    .row {
        height: 140px;
        align-items: center;
    }

    .social-link-wrapper {
        display: flex;
        justify-content: space-between;
        flex-direction: row;

        .social-link {
            margin: 0px;
            color: $color__orange;
            border: 1px solid $color__orange;
        }
    }
}