.section {
    padding-top: 50px;
    padding-bottom: 50px;
    position: relative;
    z-index: 1;

    &.--about-floater:after {
        content: url('./dist/images/about-image-v2.png');
        position: absolute;
        display: block;
        z-index: -1;
        top: -30%;
        right: 0;

        @media screen and (max-width: 1230px) {
            content: "";
            top: -20%;
            width: 100%;
            height: 100%;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center right;
            background-image:  url('./dist/images/about-image-v2.png');
        }

        @media screen and (max-width: 900px) {
            top: 5%;
            opacity: 0.3;
        }
    }
}

.section-header {
    &.--aquagreen {
        color: $color__aquagreen;

        &.--underline:after {
            content: "";
            height: 6px;
            width: 50px;
            left: -15px;
            display: block;
            position: relative;
            margin-top: 28px;
            margin-bottom: 40px;
            background-color: $color__aquagreen;
        }
    }

    &.--orange {
        color: $color__orange;
    }
    .title[data-text-bg],
    &[data-text-bg] {
        position: relative;

        @media screen and (max-width: 425px) {
            overflow: hidden;
        }

        &:before {
            content: attr(data-text-bg);
            font-size: 100px;
            font-weight: 700;
            line-height: 81px;
            opacity: 0.05;
            z-index: -1;
            display: block;

            top: 0;
            position: absolute;
        }

        &.--bottom:before {
            top: auto;
            bottom: 0;
        }

        &.--left:before {
            left: -25%;
        }

        &.--center::before {
            text-align: center;
            width: 100%;
        }

        &.--aquagreen:before {
            color: $color__aquagreen;
        }

        &.--orange:before {
            color: $color__orange;
        }
    }

    .title {
        font-size: 60px;
        line-height: 56px;
        margin: 0;
    }

    .subtitle {
        color: #FFF;
        font-size: 18px;
        font-weight: 700;
        line-height: 24px;
    }
}


.section-news {
    position: relative;

    &.--news-floater:after {
        content: url('./dist/images/news-after.png');
        display: block;
        position: absolute;
        bottom: -20px;
        left: 0;
        z-index: -1;
    }
    
    .mini-slogan {
        color: rgba(#4A4A4A, .6);
    }
}

.data-text-bg {
    position: relative;
    &:before {
        content: attr(data-text-bg);
        font-size: 100px;
        font-weight: 700;
        line-height: 81px;
        opacity: 0.05;
        z-index: -1;
        display: block;

        top: 0;
        position: absolute;
    }

    &.--left:before {
        left: -35px;
    }

    &.--center::before {
        text-align: center;
        width: 100%;
    }

    &.--aquagreen:before {
        color: $color__aquagreen;
    }

    &.--orange:before {
        color: $color__orange;
    }
}

.section-wellness {

    .link-card {
        &:hover {
            text-decoration: none;
        }

        .link-image {
            height: 240px;
            overflow: hidden;
            object-fit: cover;
            border-radius: 15px;
            box-shadow: 0 3px 24px 0 rgba(63,92,117,0.10);
            
            img {
                height: 100%;
            }
        }

        h5 {
            font-size: 18px;
            margin-top: 20px;
            font-weight: 600;
            letter-spacing: 0;
            color: $color__text-main;
        }
    }
}

.section-bg-before {
    position: relative;
    z-index: 1;

    &:before {
        content: '';
        
        top: 0;
        z-index: -1;
        width: 100%;
        height: 575px;
        display: block;
        position: absolute;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url('./dist/images/bg-promote-wellness.jpg');
    }
}
