.card.accordion {
    border: 0px;
    margin-bottom: 20px;
    box-shadow: 0 3px 45px 0 rgba(63,92,117,0.10);

    .card-header {
        background: none;
        padding: 30px;
        border: 0;
    }

    .card-body {
        padding: 0 30px 30px;
    }

    hr {
        background-color: rgba(#efefef,0.8);
        margin: 0 0 30px 0;
    }

    .btn-collapse-trigger {
        border: 0;
        width: 100%;
        padding: 0px;
        display: flex;
        background: none;
        align-items: center;
        justify-content: space-between;

        &:hover, &:focus, &:active, &:focus:active {
            outline: none;
        }

        h6 {
            margin: 0;
            font-weight: 600;
        }

        i.fas {
            transition: all 0.3s;
        }

        &:not(.collapsed) {
            i.fas {
                transform: rotate(-180deg);
            }
        }
    }

    .text-content {
        margin: 0;
    }
}