.diet-goals {
    input[type='radio'] {
        display: none;

        &:checked ~ .diet-goal-box {
            border: 2px solid #38B3BA;
        }
    }

    label {
        padding: 15px;
        width: 262px;
        vertical-align: top;
    }

    .diet-goal-box {
        padding: 30px;
        background: #FFF;
        border: 2px solid transparent;
        box-shadow: 0 3px 24px 0 rgba(63,92,117,0.10);
        border-radius: 15px;
        min-height: 277px;

        h6 {
            font-size: 18px;
            font-weight: 600;
            letter-spacing: 0;
            margin-bottom: 20px;
        }
    }
}