.modal {
    .modal-lg {
        max-width: 750px;
    }

    .modal-xl {
        max-width: 1226px;
    }

    .close {
        position: absolute;
        color: #fff;
        padding: 5px 10px;
        opacity: 0.9;
        right: -35px;
        top: -48px;

        &:hover, &:focus, &:active, &:focus:active {
            opacity: 1 !important;
            outline: none;
        }

        i.fas {
            font-size: 24px;
        }
    }

    .btn-primary {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 30px;
        margin-left: auto;
        box-shadow: 0px 25px 50px -9px rgba(0,0,0,0.08);

        i.fas {
            margin-left: 10px;
            font-size: 30px;
            line-height: 0.6;
        }
    }

    .modal-dialog {
        padding-top: 4rem;
    }

    .modal-title {
        font-size: 60px;
        line-height: 1;
        
        &[data-text-bg] {
            position: relative;
            z-index: 1;

            &:before {
                content: attr(data-text-bg);
                font-size: 100px;
                font-weight: 700;
                line-height: 81px;
                width: 100%;
                opacity: 0.05;
                z-index: -1;
                display: block;
                text-align: center;
                color: $color__aquagreen;

                top: 0;
                position: absolute;
            }
        }
    }

    .modal-content {
        padding: 35px;
        border-radius: 0px;
    }

    .modal-header {
        border-bottom: 0;
    }

    .width-100p {
        width: 100%;
    }
}