.wpcf7-form .wpcf7-response-output {
    margin: 0px;
    padding: 15px;
    color: #fff;
    margin-bottom: 25px;

    &.wpcf7-acceptance-missing {
        border-color: $color__orange;
        background-color: $color__orange;
    }

    &.wpcf7-mail-sent-ok {
        border-color: $color__aquagreen;
        background-color: $color__aquagreen;
    }
}