@font-face {
    font-family: "Proxima Nova";
    src: url("./dist/fonts/ProximaNova-Black.eot"); /* IE9 */
    src: url("./dist/fonts/ProximaNova-Black.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("./dist/fonts/ProximaNova-Black.woff") format("woff"), /* chrome, firefox */
    url("./dist/fonts/ProximaNova-Black.ttf") format("truetype"), /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
    url("./dist/fonts/ProximaNova-Black.svg#Proxima Nova Bl") format("svg"); /* iOS 4.1- */
    font-style: normal;
    font-weight: 900;
}

