.tab-header {
    margin-bottom: 30px;
    justify-content: center;

    button {
        border: none;
        font-size: 21px;
        font-weight: 700;
        background: none;
        text-align: center;
        color: rgba($color__text-main, .2);

        &:hover {
            color: $color__text-main;
        }

        &:active, &:focus, &:focus:active {
            outline: none;
        }

        &:after {
            content: "";
            height: 4px;
            width: 30px;
            display: block;
            margin: 15px auto 0 auto;
            background-color: transparent;
        }

        &.active {
            color: $color__text-main;

            &:after {
                background-color: $color__orange;
            }
        }
    }
}

.tab-row {
    display: flex;

    @media screen and (max-width: 1199px) {
        flex-wrap: wrap;
    }

    @media screen and (max-width: 991px) {
        justify-content: center;
    }

    &:first-of-type {
        .tab-row-content .day { display: block }
    }

    .tab-row-header {
        flex: 1;
        display: flex;
        padding-right: 25px;
        align-items: center;

        @media screen and (max-width: 1199px) {
            width: 100%;
            flex-basis: 100%;
        }
        
        h5 {
            flex: 1;
            margin: 0;
            font-size: 21px;
            font-weight: 700;
            text-align: right;
            color: $color__orange;

            @media screen and (max-width: 1199px) {
                text-align: left;
                margin-top: 30px;
                margin-bottom: 20px;
            }
            
            @media screen and (max-width: 991px) {
                text-align: center;
            }
        }
    }

    .tab-row-content {
        padding: 7.5px;

        .day {
            display: none;
            font-size: 13px;
            font-weight: 700;
            text-align: center;
            margin-bottom: 20px;
            letter-spacing: 1.86px;
            text-transform: uppercase;
            color: rgba($color__text-main, .2);

            @media screen and (max-width: 1199px) {
                display: block;
            }
        }
        
        .tab-row-square {
            width: 166px;
            padding: 12px;
            height: 166px;
            display: flex;
            background: #FFF;
            align-items: center;
            justify-content: center;
            box-shadow: 0 3px 45px 0 rgba(63,92,117,0.10);
        }

        p {
            margin: 0px;
            font-size: 14px;
            line-height: 24px;
            text-align: center;
        }
    }
}