button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: $color__border-button;
	border-radius: 3px;
	background: $color__background-button;
	color: rgba(0, 0, 0, .8);
	@include font-size(0.75);
	line-height: 1;
	padding: .6em 1em .4em;

	&:hover {
		border-color: $color__border-button-hover;
	}

	&:active,
	&:focus {
		border-color: $color__border-button-focus;
	}
}

%btn {
	border-radius: 26.5px;
	padding: 16px 24px;
	font-weight: 700;
	font-size: 16px;
	line-height: 1;
}

.btn {
	@extend %btn;
}

.btn-primary {
	color: #fff;

	&.--orange {
		background-color: $color__orange;
		border-color: $color__orange;

		&:hover {
			background-color: darken($color__orange, 10);
			border-color: darken($color__orange, 10);
		}
	}

	&.--aquagreen {
        background-color: $color__aquagreen;
		border-color: $color__aquagreen;

		&:hover, &:focus, &:active, &:focus:active {
			outline: none;
			box-shadow: none;
			background-color: darken($color__aquagreen, 2);
			border-color: darken($color__aquagreen, 2);
		}
	}
}

.btn-outline {
	color: #fff;
	border: 2px solid #fff;

	&:hover {
		color: #fff;
		border: 2px solid #fff;
	}
}

.btn-primary-outline {
	background-color: #fff;

	&.--aquagreen {
		color: $color__aquagreen;
		border: 2px solid $color__aquagreen;

		&:hover, &:focus, &:active, &:focus:active {
			background-color: #fff;
			color: $color__aquagreen;
			border: 2px solid $color__aquagreen;
		}
	}
}

.btn-order-now {
	a {
		@extend %btn;
		background-color: $color__orange;
		border-color: $color__orange;

		&::before {
			content: '\f07a';
			font-family: $font__icon;
			margin-right: 10px;
		}
	}
}