@import "buttons";

@import "fields";

.form-group {
    margin-bottom: 1.3rem;

    label {
        font-size: 14px;
        line-height: 17px;
        margin-bottom: 0.4rem;
        color: rgba($color__text-main, .67);
    }

    .form-control {
        border: 0;
        height: auto;
        padding: 13px 15px;
        border-radius: 4px;
        background-color: #F6F7FA;
        color: $color__text-main;

        &:focus {
            box-shadow: 0 0 0 0.2rem rgba($color__aquagreen, 0.3);
        }
    }

    select.form-control {
        height: 50px;
    }

    textarea.form-control {
        height: 160px;
        min-height: 160px;
    }

    
}

.progress-form {
    .progress-steps {
        display: none;

        &.active {
            display: block;
        }
    }
}

.form-unit {
    position: relative;

    .form-control {
        &.--p20 {
            padding-right: 25px;
        }
        padding-right: 30px;
    }

    &:after {
        top: 50%;
        right: 10px;
        display: block;
        font-size: 12px;
        position: absolute;
        content: attr(data-unit);
        transform: translateY(-50%);
        color: darken($color__form_icon, 50);
    }
}

.form-icon {
    position: relative;

    & > .form-control {
        padding-left: 42px;
    }

    &:before {
        content: '';
        top: 50%;
        left: 15px;
        display: block;
        font-size: 16px;
        position: absolute;
        color: $color__form_icon;
        font-family: $font__icon;
        transform: translateY(-50%);
    }


    &.calendar:before {
        content: '\f073';
    }

    &.marker:before {
        content: "\f3c5";
        font-weight: 800;
    }
    
    &.clock:before {
        content: "\f017";
        font-weight: 800;
    }
}

.form-section-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 15px;
    
    &:not(:first-of-type) {
        margin-top: 30px;
    }
}

.form-section-title + hr {
    background-color: $color__border-color;
    margin-top: 0px;
    margin-bottom: 30px;
}